.order{
  &__inner{
    margin-top: 1.5em;
    border-radius: 1em;
    border: 1px solid $mercury;
  }
  &__top{
    padding: 1.375em 1.5em;
    background: #f5f1e9;
    border-radius: 1em 1em 0 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-weight: 700;
    font-size: 1.3125em;
    line-height: 140%;
    text-transform: uppercase;
    font-family: $font2;
  }
  &__edit{
    display: flex;
    align-items: center;
    color: $fuscous-gray;
    border-bottom: 1px solid $fuscous-gray;
    img{
      width: 1em;
      margin-right: .375em;
    }
    span{
      font-size: 1.125em;
    }
  }
  &__content{
    padding: 1.875em 1.5em;
  }
  &__items{
    @include grid100gap(1.875em);
  }
  &__bottom{
    background: #fef1bb;
    border-radius: 0 0 1em 1em;
  }
  &__sum{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    padding: .625em 1.875em;
    border-bottom: 1px solid #ecdfa8;
    &:last-child{
      border-bottom: none;
    }
    &-title{
      font-size: 1.3125em;
      line-height: 140%;
    }
    &-num{
      font-family: $font2;
      font-weight: 700;
      font-size: 1.6875em;
      line-height: 140%;
      margin-left: .7em;
    }
  }
  &__hint{
    display: flex;
    align-items: flex-start;
    margin: 2.75em 1.5em 0;
    max-width: 28em;
    img{
      width: 1.625em;
      margin-right: .75em;
      flex-shrink: 0;
    }
    &-text{
      font-size: 1.125em;
      a{
        font-weight: 700;
      }
    }
  }
}

.order-item{
  display: grid;
  grid-template-columns: 8em calc(100% - 10.25em);
  gap: 2.25em;
  font-family: $font2;
  &__img{
    img{
      width: 100%;
      display: block;
    }
  }
  &__content{
    padding-top: 1.875em;
  }
  &__title{
    font-weight: 700;
    font-size: 1.125em;
    line-height: 140%;
    text-transform: uppercase;
    text-decoration: underline;
    text-decoration-skip-ink: none;
    color: $tango;
  }
  &__info{
    margin-top: .625em;
    display: flex;
    &-count,
    &-title{
      font-weight: 700;
      font-size: 1.3125em;
      line-height: 140%;
    }
    &-count{
      margin-left: 1.4em;
    }
  }
}

.order-promo{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - .4375em));
  gap: .875em;
  align-items: flex-end;
  margin-top: 1.875em;
  &__btn{
    height: 2.75em;
    width: 100%;
    span{
      font-size: 1.125em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .order{
    &__inner{
      margin-top: 1.25em;
      border-radius: .625em;
    }
    &__top{
      padding: 1em;
      border-radius: .625em .625em 0 0;
    }
    &__title{
      font-size: .875em;
    }
    &__edit{
      img{
        width: .6875em;
        margin-right: .25em;
      }
      span{
        font-size: .75em;
      }
    }
    &__content{
      padding: 1.25em .375em;
    }
    &__items{
      gap: 1.25em;
    }
    &__bottom{
      border-radius: 0 0 .625em .625em;
    }
    &__sum{
      padding: .5em 1.25em;
      &-title{
        font-size: .875em;
      }
      &-num{
        font-size: 1.125em;
      }
    }
    &__hint{
      margin: 1.25em 0 0;
      img{
        width: 1em;
        margin-right: .5em;
      }
      &-text{
        font-size: .75em;
        line-height: 140%;
      }
    }
  }

  .order-item{
    grid-template-columns: 4.25em calc(100% - 6.875em);
    gap: .625em;
    &__content{
      padding-top: 0;
    }
    &__title{
      font-size: .6875em;
    }
    &__info{
      justify-content: space-between;
      &-count,
      &-title{
        font-size: .875em;
      }
    }
  }

  .order-promo{
    grid-template-columns: repeat(2,calc(50% - .3125em));
    gap: .625em;
    &__btn{
      height: 1.875em;
      span{
        font-size: .75em;
      }
    }
  }
}
