.order-form{
  margin-top: 6em;
  &__group{
    margin-top: 3.75em;
    &:first-child{
      margin-top: 0;
    }
  }
  &__title{
    font-size: 1.875em;
    line-height: 140%;
    font-weight: 700;
    font-family: $font2;
    margin-bottom: .6em;
  }
  &__hint{
    font-size: 1.125em;
    line-height: 90%;
    font-style: italic;
    color: $primary;
    margin-top: 3em;
  }
  &__btn{
    width: 28.5em;
    max-width: 100%;
    margin: 3em auto 0;
  }
}

.user-data{
  @include grid100gap(1em);
  &__hint{
    display: flex;
    color: #A1A1A1;
    font-style: italic;
    font-size: 1.125em;
    line-height: 150%;
    &:before{
      content: '—';
      margin-right: 1em;
    }
  }
}

.add-service{
  @include grid100gap(.625em);
  &__title{
    font-size: 1.125em;
    line-height: 140%;
    color: #3d3d3d;
  }
}

.radio-btn{
  input{
    &:checked~.radio-btn__inner{
      background-color: #F3FAE2;
      .radio-btn__icon{
        border: none;
        background-size: 100% auto;
      }
    }
  }
  &__inner{
    transition: all .3s;
    padding: 1.25em .75em;
    display: flex;
  }
  &__icon{
    width: 1.25em;
    height: 1.25em;
    border: 1px solid #767676;
    border-radius: 50%;
    flex-shrink: 0;
    background-image: url(../img/radio.svg);
    @include bgImgSize(0%);
    margin-right: 1.125em;
    margin-top: .125em;
  }
  &__title{
    font-size: 1.125em;
    line-height: 160%;
    font-weight: 700;
  }
  &__desc{
    font-size: 1.125em;
    line-height: 125%;
    color: #818181;
    margin-top: .5em;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .order-form{
    margin-top: 2.5em;
    &__group{
      margin-top: 2.5em;
    }
    &__title{
      font-size: 1.25em;
      margin-bottom: 1.3em;
    }
    &__hint{
      font-size: .75em;
      line-height: 133%;
      margin-top: 3.5em;
    }
    &__btn{
      margin-top: 1.5em;
      width: 100%;
    }
  }

  .user-data{
    gap: .625em;
    &__hint{
      font-size: .75em;
    }
  }

  .add-service{
    gap: .375em;
    &__title{
      font-size: .75em;
    }
  }

  .radio-btn{
    &__inner{
      padding: 1.125em .375em;
    }
    &__icon{
      margin-top: 0;
      width: .875em;
      height: .875em;
      margin-right: .75em;
    }
    &__title,
    &__desc{
      font-size: .75em;
      line-height: 125%;
    }
  }
}
