.product-cart{
  padding: 2em;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 2.5em;
  &__price{
    font-size: 3.0625em;
    line-height: 140%;
    font-family: $font2;
  }
  &__btn{
    width: 16.25em;
    height: 3.625em;
    margin-top: .5em;
    border-radius: .3125em;
    span{
      font-size: 1.1875em;
    }
    img{
      width: 1.75em;
      margin-right: 2em;
    }
  }
}

.add-info{
  margin-top: 5em;
  border-radius: 1em;
  border: 1px dashed $mercury;
  &__top{
    font-size: 1em;
    border-bottom: 1px solid $bone;
    height: 5.5em;
    @include center;
    background-color: $wild-sand;
    border-radius: 1em 1em 0 0;
    .RatingBadgeWidget{
      width: 14.75em;
      height: 5em;
      display: block;
      @include bgImgSize(100%);
    }
  }
  &__bottom{
    padding: 1em 2em 1.25em;
    @include grid100gap(.625em);
  }
  &__title,
  &__title2{
    text-align: center;
    font-weight: 700;
    font-size: 1.125em;
    line-height: 167%;
    color: $tango;
  }
  &__title{
    color: $red;
  }
  &__desc{
    width: 31.1em;
    max-width: 100%;
    margin: 0 auto;
    font-size: 1.125em;
    color: $tango;
    text-align: center;
  }
  &__hint{
    display: flex;
    align-items: flex-start;
    padding-top: .625em;
    border-top: 1px solid $mercury;
    margin-top: 1.5em;
    color: $fuscous-gray;
    img{
      width: 1.5em;
      margin-right: .875em;
    }
    span{
      font-size: 1.125em;
      line-height: 167%;
      margin-top: -.125em;
    }
    br{
      display: none;
    }
  }
}

.product-detail{
  margin-top: 5em;
  &__tabs{
    display: flex;
  }
  &__tab{
    height: 5em;
    display: flex;
    font-weight: 700;
    font-family: $font2;
    padding: 0 1em;
    background: linear-gradient(180.00deg, rgb(245, 241, 233),rgb(255, 255, 255) 100%);
    border-radius: 1em 1em 0 0;
    border: 1px solid $mercury;
    position: relative;
    z-index: 4;
    transition: all .3s;
    &:nth-child(2){
      z-index: 3;
    }
    &:nth-child(3){
      z-index: 2;
    }
    &:nth-child(4){
      z-index: 1;
    }
    &:not(:first-child){
      margin-left: -1.5em;
      padding-left: 2.25em;
    }
    &._active{
      background: $white;
      span{
        &:before{
          opacity: 0;
        }
      }
    }
    span{
      font-size: 1.3125em;
      line-height: 281%;
      position: relative;
      &:before{
        content: '';
        position: absolute;
        width: 100%;
        height: 1px;
        border-bottom: 1px dotted $dune;
        left: 0;
        bottom: 1.25em;
        transition: all .3s;
      }
    }
  }
  &__content{
    padding: 2.25em .5em 1.125em;
    border-radius: 1em;
    border: 1px solid $mercury;
    background-color: $white;
    position: relative;
    z-index: 5;
    margin-top: -1.25em;
  }
  .text{
    p{
      line-height: 120%;
    }
  }
}

.props{
  @include grid100gap(2em);
  &__group{
    &:not(:last-child){
      border-bottom: 1px solid $spring-wood;
    }
  }
}

.prop-item{
  padding: .5em 1.125em;
  display: flex;
  justify-content: space-between;
  &:nth-child(odd){
    background-color: $spring-wood;
  }
  &__title,
  &__desc{
    font-size: 1.0625em;
    line-height: 140%;
  }
  &__title{
    font-weight: 700;
  }
  &__desc{
    max-width: 15.6em;
    text-align: right;
    a{
      text-decoration: underline;
      color: $primary;
    }
  }
}

.product-feedback{
  &__nav{
    display: flex;
    margin-top: 1em;
  }
  &__btn{
    padding: 0 3em;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .product-cart{
    padding: 1.25em;
    margin-top: 1.25em;
    &__price{
      font-size: 1.875em;
    }
    &__btn{
      width: 9.75em;
      height: 2.25em;
      margin-top: .1875em;
      border-radius: .1875em;
      span{
        font-size: .75em;
      }
      img{
        width: 1.0625em;
        margin-right: 1em;
      }
    }
  }

  .add-info{
    margin-top: 2.5em;
    border-radius: .625em;
    &__top{
      height: 3.375em;
      border-radius: .625em .625em 0 0;
      .RatingBadgeWidget{
        width: 9.375em;
        height: 100%;
      }
    }
    &__bottom{
      padding: .625em 1.25em 1.5em;
      gap: .375em;
    }
    &__title,
    &__title2{
      text-align: left;
      font-size: .75em;
    }
    &__desc{
      text-align: left;
      font-size: .75em;
      line-height: 100%;
    }
    &__hint{
      padding-top: .375em;
      margin-top: 1em;
      img{
        width: 1em;
        margin-right: .5em;
      }
      span{
        font-size: .75em;
        margin-top: -.125em;
      }
      br{
        display: unset;
      }
    }
  }

  .product-detail{
    margin-top: 2.5em;
    &__tab{
      height: 2.875em;
      padding: 0 .625em;
      border-radius: .5em .5em 0 0;
      &:not(:first-child){
        margin-left: -.75em;
        padding-left: 1.25em;
      }
      span{
        font-size: .75em;
      }
    }
    &__content{
      padding: 1.25em .3125em 1.25em;
      border-radius: .5em;
      margin-top: -.75em;
    }
  }

  .props{
    gap: 1.125em;
  }

  .prop-item{
    padding: .3125em .625em;
    &__title,
    &__desc{
      font-size: .625em;
    }
  }
}
