@import "../vars.scss";
.breadcrumbs {
    a{
        font-size: 1.125em;
        line-height: 163%;
        text-decoration: underline;
        text-decoration-skip-ink: none;
        display: inline;
        position: relative;
        &:not(:first-child) {
            margin-left: 1.5em;
            &:before {
                content: '';
                position: absolute;
                z-index: 1;
                width: .39em;
                height: .62em;
                background-image: url(../img/bread-arrow.svg);
                @include bgImgSize(100%);
                top: .375em;
                right: 100%;
                margin-right: .625em;
            }
        }
        &:last-child{
            color: $primary;
        }
    }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
    .breadcrumbs{
        font-size: .67em;
    }
}
