.contacts {
  padding-top: 0.625em;
  &__title {
    text-align: center;
  }
  &__phone {
    margin-top: 20px;
    p,
    a {
      display: block;
      text-align: center;
      font-size: 0.875em;
      line-height: 120%;
      font-weight: 700;
      color: #F48729;
    }
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    margin-top: 1.875em;
    @media screen and (min-width: 577px){
      font-size: 1.33em;
    }
  }
  &__item {
    p {
      font-size: 0.75em;
    }
  }
  .map {
    position: relative;
    padding-bottom: 72%;
    margin-top: 1.875em;
    iframe {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }
}

.contacts-info {
  &__title {
    text-align: center;
  }
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    margin-top: 1.875em;
    @media screen and (min-width: 577px){
      font-size: 1.33em;
    }
  }
}
.contacts-info__card {
  &-list {
    display: grid;
    grid-template-columns: 100%;
    gap: 0.625em;
    counter-reset: section;
    li {
      display: flex;
      font-size: 0.75em;
      line-height: 120%;
      counter-increment: section;
      &:before {
        content: counter(section) '. ';
      }
    }
  }
  &-text {
    p {
      font-size: 0.75em;
      line-height: 120%;
    }
  }
  &-img {
    margin-top: 0.625em;
    img {
      width: 100%;
    }
  }
}
