.catalog{
  overflow: hidden;
  &__items{
    display: grid;
    grid-template-columns: repeat(3,calc(100% / 3 - 1em));
    gap: 4.375em 1.5em;
    margin-top: 1.875em;
  }
}

.catalog-group{
  margin-top: 3.625em;
  .sales-slider{
    margin-top: .5em;
  }
}

.line-title{
  font-family: $font2;
  font-size: 1.4375em;
  line-height: 122%;
  text-decoration: underline;
}

.catalog-group2{
  margin-top: 4.375em;
  .sales-slider2{
    margin-top: 1.875em;
  }
}

.product-card2{
  padding-top: 0;
  .product-card__badges{
    position: absolute;
    top: 0;
    right: 0;
    z-index: 2;
  }
  .product-card__img{
    height: unset;
    @include imgCover(57.6%);
    border-radius: .4375em;
    margin-bottom: 3em;
  }
  .product-card__title{
    text-align: left;
  }
  .product-card__prices{
    align-items: flex-start;
  }
  .product-card__price{
    font-size: 1.9375em;
  }
}

.sales-slider2{
  .swiper{
    &-container{
      width: calc(100% + 1.5em);
    }
    &-slide{
      width: calc(100% / 3);
      padding-right: 1.5em;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .catalog{
    &__items{
      grid-template-columns: repeat(2,calc(50% - .625em));
      gap: 2.5em 1.25em;
      margin-top: 2.5em;
    }
  }

  .catalog-group{
    margin-top: 2.5em;
    .sales-slider{
      margin-top: 1.125em;
    }
  }

  .line-title{
    font-size: 1.15em;
    line-height: 140%;
  }

  .catalog-group2{
    margin-top: 3.5em;
    .sales-slider2{
      margin-top: 1.25em;
    }
  }

  .product-card2{
    .product-card__img{
      border-radius: .25em;
      margin-bottom: 1.75em;
    }
    .product-card__price{
      font-size: 1.125em;
    }
  }

  .sales-slider2{
    .swiper{
      &-container{
        width: calc(100% + 1.25em);
      }
      &-slide{
        width: 50%;
        padding-right: 1.25em;
      }
    }
  }
}
