.instalment{
  &__inner{
    margin-top: 1.25em;
    @media screen and (min-width: 577px){
      font-size: 1.33em;
    }
  }
  &__desc{
    font-size: .75em;
    line-height: 130%;
  }
  &__bank{
    width: 18.5em;
    display: block;
    margin: .625em 0;
  }
  &__sum{
    font-size: 1.0625em;
    text-transform: uppercase;
    font-weight: 700;
    line-height: 150%;
    padding: .625em 0;
    margin-top: .75em;
    border-bottom: 1px solid #DADADA;
    span{
      color: $primary;
      font-size: 1.125em  ;
    }
  }
}

.first-pay{
  margin-top: 1.875em;
  &__title{
    font-size: 1.0625em;
    font-weight: 700;
    text-transform: uppercase;
  }
  &__items{
    border: 1px solid #D6D3CC;
    overflow: hidden;
    border-radius: .5em;
    margin-top: 1.25em;
  }
  &__item{
    &:last-child{
      .first-pay__item-inner{
        border-bottom: none;
      }
    }
    input:checked{
      &~.first-pay__item-inner{
        color: $white;
        background-color: $primary;
        .first-pay__item-icon{
          background-size: 0 auto;
          &:after{
            opacity: 1;
          }
        }
      }
    }
    &-inner{
      border-bottom: 1px solid #D6D3CC;
      display: flex;
      align-items: center;
      height: 3.25em;
      padding: 0 .625em;
      color: #5C584B;
    }
    &-icon{
      width: 1.125em;
      height: 1.125em;
      flex-shrink: 0;
      background-image: url(../img/check3.png);
      @include bgImgSize(100%);
      margin-right: .75em;
      position: relative;
      &:after{
        content: '';
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        background-image: url(../img/check4.png);
        @include bgImgSize(100%);
        opacity: 0;
      }
    }
    &-num{
      font-size: 1.375em;
    }
    &-text{
      font-size: 1.0625em;
      margin-left: .25em;
    }
  }
}

.instalment-req{
  margin-top: 1.875em;
  border-radius: 1.25em;
  border: 1px solid #E7E7E7;
  padding: 1.25em .625em .625em;
  &__fields{
    padding: 0 .625em;
    margin-top: 1.25em;
    @include grid100gap(1em);
  }
  .fg{
    input{
      border-radius: .875em;
      border-color: #D6D3CC;
      background-color: transparent;
    }
  }
  &__text{
    font-size: .75em;
    line-height: 133%;
    color: #5C584B;
    margin-top: 1.6em;
    a{
      text-decoration: underline;
      color: $primary;
    }
  }
  &__sum{
    margin-top: 1.25em;
    &-item{
      font-size: .75em;
      line-height: 133%;
      font-weight: 700;
      text-transform: uppercase;
      span{
        color: #5C584B;
        font-weight: 400;
      }
    }
  }
  &__btn{
    width: 100%;
    margin-top: 1.25em;
    padding: 0;
    height: 1.875em;
    border-radius: .625em;
    span{
      font-size: .75em;
    }
  }
}

.instalment-text{
  p{
    line-height: 125%;
  }
  h3{
    text-transform: unset;
    font-family: $font1;
  }
}


