.header{
  padding: 1.75em 0 2.25em;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo{
    display: block;
    flex-shrink: 0;
    width: 11em;
    img{
      width: 100%;
      display: block;
    }
  }
  &._open{
    .mob-menu{
      opacity: 1;
      pointer-events: unset;
      &__inner{
        transform: translateX(0);
      }
    }
  }
}

.header-call{
  font-family: $font2;
  text-align: right;
  &__link{
    font-size: 1.25em;
    font-weight: 700;
  }
  &__title{
    font-size: 1em;
    font-weight: 500;
  }
}

.header-action{
  display: flex;
  align-items: center;
  &__buttons{
    display: flex;
    margin-left: 1.375em;
  }
  &__btn{
    width: 3.375em;
    height: 2.625em;
    border: 1px solid $alto;
    border-radius: .4375em;
    @include center;
    margin-right: .75em;
    &:last-child{
      margin-right: 0;
    }
    img{
      height: 1.0625em;
    }
  }
}

.burger{
  flex-shrink: 0;
  @include center;
  width: 1.6875em;
  margin-right: 1.875em;
  img{
    width: 100%;
  }
}

.catalog-btn{
  @include center;
  padding: 0 1.75em;
  height: 2.625em;
  background-color: $primary;
  color: $white;
  font-weight: 500;
  text-transform: uppercase;
  font-family: $font2;
  border-radius: .4375em;
  span{
    font-size: 1.25em;
  }
}

.mob-menu{
  pointer-events: none;
  opacity: 0;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 100;
  &__inner{
    transition: all .3s;
    transform: translateX(-120%);
    position: relative;
    z-index: 3;
    height: $vh;
    width: 17.5em;
    background-color: $white;
    overflow-y: auto;
    padding: .75em 1.25em;
  }
  &__bg{
    background: rgba(0, 0, 0, 0.6);
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
  }
  &__title{
    font-size: .75em;
    line-height: 133%;
    color: $fuscous-gray;
    font-family: $font3;
  }
}

.menu-item{
  border-bottom: 1px solid #E4E4E4;
  font-family: $font2;
  &__show{
    display: flex;
    align-items: center;
    height: 3.375em;
    &:after{
      content: '';
      width: .3125em;
      height: .5em;
      background-image: url(../img/arrow-right.svg);
      @include bgImgSize(100%);
      margin-left: 1.25em;
    }
    a{
      font-size: .8125em;
      font-weight: 700;
    }
  }
  &__items{
    margin-top: .625em;
  }
  &._open{
    &>.sub-menu{
      opacity: 1;
      pointer-events: unset;
    }
  }
}

.sub-menu{
  transition: all .3s;
  opacity: 0;
  pointer-events: none;
  position: fixed;
  top: 0;
  left: 0;
  height: $vh;
  width: 17.5em;
  background-color: $white;
  overflow-y: auto;
  padding: .75em 1.25em;
  &__back{
    display: flex;
    align-items: center;
    color: $fuscous-gray;
    font-family: $font3;
    img{
      width: .3125em;
      margin-right: .625em;
    }
    span{
      font-size: .75em;
      line-height: 133%;
    }
  }
  &__title{
    font-weight: 700;
    font-size: .875em;
    color: $tango;
    margin-top: 1.25em;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .header{
    padding: 1.25em 0 1.875em;
    &__inner{
      flex-wrap: wrap;
    }
    &__logo{
      width: 8em;
    }
  }

  .header-action{
    width: 100%;
    margin-top: .75em;
    order: 2;
    &__buttons{
      margin-left: auto;
    }
    &__btn{
      height: 1.875em;
      width: 2.5em;
      border-radius: .3125em;
      margin-right: .5625em;
      img{
        height: .8125em;
      }
    }
  }

  .header-call{
    &__link{
      font-size: .875em;
    }
    &__title{
      font-size: .6875em;
    }
  }

  .burger{
    width: 1.25em;
    margin-right: 1em;
  }

  .catalog-btn{
    height: 1.875em;
    padding: 0 1.25em;
    border-radius: .3125em;
    span{
      font-size: .9375em;
    }
  }
}
