.basket {
  padding: 1.875em 0 3.125em;
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 40px;
    margin-top: 1.875em;
  }
  &__group {
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #FFF1B9;
    border: 0.0625em solid #E0DCD0;
    border-radius: 0.625em;
    padding: 1.25em;
    margin-top: 4.375em;
  }
  &__total-price {
    position: relative;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    padding-bottom: 1.25em;
    &:after {
      content: '';
      position: absolute;
      left: -1.25em;
      bottom: 0;
      width: calc(100% + 2.5em);
      height: 0.0625em;
      background: #E0DCD0;
    }
    span {
      font-size: 1.125em;
      line-height: 120%;
      color: #64482A;
    }
    strong {
      font-size: 1.3125em;
      line-height: 120%;
      font-weight: 700;
      font-family: $font2;
      margin-left: .25em;
    }
  }
  &__total-price2 {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 1.25em;
    span {
      font-size: 1.125em;
      line-height: 120%;
      color: #64482A;
    }
    strong {
      font-size: 2.25em;
      line-height: 120%;
      font-weight: 400;
      font-family: $font2;
    }
  }
  &__btns {
    width: 100%;
    display: grid;
    grid-template-columns: 100%;
    gap: 0.9375em;
    margin-top: 1.875em;
  }
  &__btn {
    padding-left: 1em;
    padding-right: 1em;
  }
  &__text {
    display: grid;
    grid-template-columns: 100%;
    gap: 1em;
    margin-top: 2.5em;
    p {
      font-size: 1.125em;
      line-height: 120%;
      font-weight: 700;
    }
  }
}

.basket-card {
  position: relative;
  display: flex;
  gap: 2em;
  padding-right: 2em;
  &__img {
    width: 7.9375em;
    flex-shrink: 0;
    img {
      width: 100%;
    }
  }
  &__desc {
    display: flex;
    flex-direction: column;
    align-items: start;
    padding-top: 1.875em;
  }
  &__title {
    margin-bottom: 0.3125em;
    p {
      font-family: 'PF Agora Sans Pro';
      line-height: 130%;
      color: #F27623;
      text-transform: uppercase;
      text-decoration: underline;
      text-underline-offset: 0.0625em;
    }
  }
  &__price {
    display: inline-block;
    span {
      font-family: 'PF Agora Sans Pro';
      font-size: 1.6875em;
      line-height: 120%;
    }
  }
  .cartcalc {
    margin: 1.875em 0;
  }
  &__price2 {
    border-top: 0.0625em solid #E7E7E7;
    padding-top: 0.125em;
  }
  &__delete {
    position: absolute;
    top: 0;
    right: 0;
    width: 1.875em;
    height: 1.875em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: #dcdcda;
    border-radius: 50%;
    padding: 0.1875em;
    cursor: pointer;
    svg {
      max-width: 100%;
    }
  }
}


@media screen and (max-width: 576px) {
  .basket {
    padding: 1.25em 0;
    &__cards {
      gap: 1.25em;
      margin-top: 1.25em;
    }
    &-card {
      gap: 1.25em;
      padding-right: 1em;
      &__img {
        width: 5.3125em;
      }
      &__desc {
        padding-top: 1.25em;
      }
      &__title {
        p {
          font-size: 0.75em;
        }
      }
      .cartcalc {
        margin: 1.25em 0;
      }
      &__price {
        span {
          font-size: 1.125em;
        }
      }
      &__delete {
        width: 0.875em;
        height: 0.875em;
        padding: 0.0625em;
      }
    }
    &__group {
      margin-top: 4.0625em;
    }
    &__total-price {
      span {
        font-size: 0.875em;
      }
      strong {
        font-size: 1.125em;
      }
    }
    &__total-price2 {
      span {
        font-size: 1em;
      }
      strong {
        font-size: 1.875em;
      }
    }
    &__text {
      margin-top: 1.25em;
      p {
        font-size: 0.875em;
      }
    }
  }
}
