.news2 {
  &__title {
    text-align: center;
  }
  &__cards {
    display: grid;
    grid-template-columns: 100%;
    gap: 2.5em;
    margin-top: 1.875em;
  }
  @media screen and (min-width: 577px){
    &__cards{
      font-size: 1.33em;
      grid-template-columns: repeat(2,calc(50% - .75em));
      gap: 1.5em;
    }
  }
}

.news2-card {
  &__img {
    img {
      width: 100%;
    }
  }
  &__desc {
    margin-top: 1.25em;
  }
  &__date {
    span {
      font-size: 0.75em;
      line-height: 120%;
      color: #BFBEB8;
    }
  }
  &__title {
    margin-top: 0.5em;
    p {
      font-family: 'PF Agora Sans Pro';
      font-size: 0.75em;
      line-height: 120%;
      color: #F27623;
      text-transform: uppercase;
      text-decoration: underline;
      text-underline-offset: 0.0625em;
    }
  }
  &__text {
    margin-top: 0.5em;
    p {
      font-size: 0.75em;
      line-height: 120%;
      color: #575652;
    }
  }
  &__bottom {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    align-items: center;
    gap: 0.5em;
    border-top: 0.0625em solid #E7E7E7;
    padding-top: 0.75em;
    margin-top: 0.75em;
  }
  &__group {
    display: flex;
    align-items: center;
  }
  &__comments {
    font-size: 0.75em;
    line-height: 120%;
  }
  &__category {
    font-size: 0.75em;
    line-height: 120%;
    color: #666666;
  }
  &__link {
    color: #ABAAAA;
    text-decoration: underline;
    text-underline-offset: 0.0625em;
    span {
      font-style: italic;
      font-size: 0.75em;
      line-height: 120%;
    }
  }
}

.news2-item,
.comments{
  &__inner{
    @media screen and (min-width: 577px){
      font-size: 1.33em;
    }
  }
}

.news2-item {
  &__text {
    margin-top: 1.25em;
    p {
      font-size: 0.75em;
      line-height: 120%;
      a {
        color: #F27623;
        text-decoration: underline;
        text-underline-offset: 0.0625em;
      }
    }
  }
}

.news2-item__card {
  margin-top: 0.625em;
  .news2-card__desc {
    margin-top: 0.625em;
  }
  .news2-card__date {
    margin: 1.25em 0 0 0;
  }
  .news2-card__bottom {
    justify-content: start;
    gap: 0.1875em;
    padding-top: 0.5em;
    margin-top: 0.5em;
  }
}

.comments {
  padding: 1.25em 0 2.5em;

  &__text {
    margin-top: 0.625em;
    p {
      font-size: 0.75em;
      line-height: 120%;
    }
  }
  &__btn {
    width: 100%;
    padding-left: 1em;
    padding-right: 1em;
    margin-top: 0.625em;
    text-transform: uppercase;
  }
}


