.order-thank{
  &__inner{
    text-align: center;
    margin-top: 1.25em;
    background-color: #ECFFE1;
    border-radius: .625em;
    border: 1px solid #E0DCD0;
    @media screen and (min-width: 577px){
      font-size: 1.33em;
    }
  }
  &__top{
    padding: 1em 2.25em;
  }
  &__middle{
    padding: 1em;
    border-top: 1px solid #E0DCD0;
    border-bottom: 1px solid #E0DCD0;
  }
  &__bottom{
    padding: 1.25em;
  }
  &__title{
    font-size: 1em;
    line-height: 120%;
    font-weight: 700;
  }
  &__desc,
  &__desc2{
    font-size: .875em;
    line-height: 110%;
  }
  &__desc{
    color: #5F6D56;
    font-style: italic;
  }
  &__desc2{
    color: $primary;
    margin-bottom: 1.8em;
  }
  &__phone{
    font-size: 1.5em;
    line-height: 120%;
    font-weight: 700;
  }
}
