.pagination {
  margin-top: 5em;
  display: flex;
  justify-content: center;
}
.pagination a {
  @include center;
  font-weight:bold;
  background:  linear-gradient(#FFF1B9,#fff7d8);
  @include center;
  padding: .125em .5625em;
  border-bottom: 3px solid #ded3a5;
  margin: 0 .5em;
  border-radius: .3125em;
  font-size: 1em;
  line-height: 140%;
  text-decoration: none;
  color:#5C2707;
  transition: all 0.2s linear;
  min-width: 1.75em;
  min-height: 2em;
  &:hover {
    color: #f27623;
  }
  &.is-active {
    background: linear-gradient(#f7a734, #f1671e);
    border-bottom: 3px solid #ce530e;
    border-radius:4px;
    color:#fff;
    margin-right:5px;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .pagination {
    margin-top: 3.67em;
    font-size: .75em;
  }
}
