.catalog-nav{
  display: flex;
  justify-content: space-between;
  margin-top: 1.375em;
  &__filter{
    padding: 0 1em;
    background: #fff2c0;
    height: 2.875em;
    display: flex;
    align-items: center;
    font-family: $font2;
    border-radius: .5em;
    svg{
      width: 1.125em;
      margin-right: .875em;
    }
    span{
      font-size: 1.1875em;
    }
  }
}

.sort{
  position: relative;
  border: 1px solid $alto;
  border-radius: .5em;
  transition: all .3s;
  &._open{
    border-color: transparent;
    .sort__hidden{
      opacity: 1;
      pointer-events: unset;
    }
  }
  &__show{
    padding: 0 1em;
    height: 2.875em;
    display: flex;
    align-items: center;
    position: relative;
    z-index: 4;
  }
  &__icon{
    margin-left: 1em;
    width: 1.3125em;
    flex-shrink: 0;
  }
  &__title{
    font-size: 1.1875em;
  }
  &__hidden{
    opacity: 0;
    pointer-events: none;
    transition: all .3s;
    background-color: $white;
    z-index: 3;
    position: absolute;
    border: 1px solid $alto;
    border-radius: .5em;
    top: 0;
    left: 0;
    width: 100%;
    padding: 4em 1em 2em;
  }
  &__items{
    @include grid100gap(1.5em);
  }
  &__item{
    display: flex;
    align-items: flex-start;
    font-size: 1em;
    &:before{
      content: '';
      margin: .3125em .5em 0 0;
      width: .625em;
      height: .5em;
      background-image: url(../img/check.svg);
      @include bgImgSize(100%);
      opacity: 0;
      transition: all .3s;
    }
    &._active{
      &:before{
        opacity: 1;
      }
    }
  }
}

.filter{
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 20;
  opacity: 0;
  pointer-events: none;
  &._open{
    opacity: 1;
    pointer-events: unset;
    .filter__inner{
      transform: translateX(0);
    }
  }
  @media screen and (min-width: 577px){
    font-size: 1.33em;
  }
  &__bg{
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: $vh;
    background: rgba(0, 0, 0, 0.6);
  }
  &__inner{
    transition: all .3s;
    transform: translateX(-120%);
    position: relative;
    z-index: 2;
    width: 17.5em;
    height: $vh;
    background-color: $white;
    padding: .875em 1.25em;
    overflow-y: auto;
  }
  &__title{
    color: $fuscous-gray;
  }
  &__items{
    margin-top: 1.375em;
  }
  &__nav{
    margin-top: 2.5em;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__btn{
    min-width: 10em;
  }
  &__clear{
    margin-top: 1.5em;
    display: flex;
    align-items: center;
    justify-content: center;
    color: $gray-nickel;
    img{
      margin-right: .4375em;
      width: .8125em;
      flex-shrink: 0;
    }
    span{
      text-decoration: underline;
      font-size: .75em;
      line-height: 140%;
    }
  }
}

.filter-item{
  padding: .875em 1.25em 1.25em;
  box-shadow: inset -0px -1px 0 0px $mercury;
  border-bottom: 1px solid $pampas;
  label{
    font-size: .75em;
    line-height: 140%;
    display: block;
    margin-bottom: .67em;
  }
  select{
    width: 100%;
    font-size: .875em;
    padding: 0 .33em;
    height: 1.5em;
    border: 1px solid $boulder;
    border-radius: 0;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .catalog-nav{
    font-size: .69em;
  }
}
