.gallery{
  margin-top: 3.75em;
  position: relative;
  &__zoom{
    position: absolute;
    top: 0;
    left: 0;
    width: 2.75em;
    z-index: 2;
    pointer-events: none;
    img{
      width: 100%;
      display: block;
    }
  }
  &__big{
    .gallery__img{
      @include imgCover(66.7%);
    }
  }
  &__small{
    margin-top: 1.5em;
    .gallery__img{
      padding-bottom: 100%;
      position: relative;
      border: 1px solid $mercury;
      img{
        @include centerAbsolute;
        max-width: calc(100% - 1.5em);
        max-height: calc(100% - 1.5em);
      }
    }
    .swiper{
      width: calc(100% + 1.5em);
      &-slide{
        width: 20%;
        padding-right: 1.5em;
        &-thumb-active{
          .gallery__img{
            border-width: 2px;
            border-color: $valencia;
          }
        }
      }
    }
  }
  .product-card__badges{
    position: absolute;
    top: 0;
    right: 0;
    padding: 1.5em;
    z-index: 2;
  }
  .product-card__badge{
    height: 4.5em;
  }
}

.room-size label,
.color__title{
  display: block;
  font-weight: 700;
  font-size: 1.1875em;
  line-height: 140%;
  margin-bottom: .33em;
}

.room-size{
  margin-top: 2.5em;
  select{
    border: 1px solid $alto;
    border-radius: .37em;
    width: 100%;
    height: 2.81em;
    font-size: 1.375em;
    padding: 0 1.5em 0 .72em;
    background-image: url(../img/select-arrow.svg);
    background-size: .55em auto;
    background-position: right .72em center;
    background-repeat: no-repeat;
    -webkit-appearance: none;
    -moz-appearance: none;
    -ms-appearance: none;
    -o-appearance: none;
    appearance: none;
    &::-ms-expand {
      display: none; // IE
    }
  }
}

.color{
  margin-top: 2.5em;
  &__items{
    display: flex;
    flex-wrap: wrap;
    margin-top: -.5em;
  }
}

.color-item{
  margin: .5em 1em 0 0;
  label{
    display: block;
    width: 3em;
    height: 3em;
    position: relative;
    img{
      width: 100%;
      height: 100%;
      object-fit: cover;
      border-radius: 50%;
      display: block;
    }
    &:before{
      content: '';
      @include centerAbsolute;
      width: calc(100% + .75em);
      height: calc(100% + .75em);
      border-radius: 50%;
      border: .375em solid $tango;
      background-image: url(../img/check2.svg);
      @include bgImgSize(1.1875em);
      opacity: 0;
      transition: all .3s;
      pointer-events: none;
    }
  }
  input:checked~label{
    &:before{
      opacity: 1;
    }
  }
}

.other-products{
  overflow: hidden;
  .sales-slider2{
    margin-top: 2em;
  }
}

.product-card3{
  text-align: center;
  &__img{
    height: 9.375em;
    @include center;
    margin-bottom: 1.375em;
    img{
      max-width: 100%;
      max-height: 100%;
    }
  }
  &__title{
    font-size: 1.25em;
    line-height: 140%;
    text-transform: uppercase;
    color: $tango;
    a{
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .gallery{
    margin-top: 1.25em;
    &__zoom{
      width: 1.125em;
    }
    &__small{
      margin-top: .625em;
      .gallery__img{
        img{
          max-width: calc(100% - .625em);
          max-height: calc(100% - .625em);
        }
      }
      .swiper{
        width: calc(100% + .75em);
        &-slide{
          padding-right: .75em;
          &-thumb-active{
            .gallery__img{
              border-width: 1px;
            }
          }
        }
      }
    }
    .product-card__badges{
      padding: .625em;
    }
    .product-card__badge{
      height: 1.875em;
    }
  }

  .room-size{
    margin-top: 1.25em;
    label{
      font-size: .75em;
    }
    select{
      height: 2.85em;
      font-size: .8125em;
      padding: 0 1.5em 0 .5em;
      background-position: right .5em center;
    }
  }

  .color{
    margin-top: 1.25em;
    &__title{
      font-size: .75em;
    }
    &__items{
      margin-top: -.25em;
      width: calc(100% + .625em);
    }
  }

  .color-item{
    margin: .625em .625em 0 0;
    label{
      width: 1.75em;
      height: 1.75em;
      &:before{
        width: calc(100% + .5em);
        height: calc(100% + .5em);
        background-size: .75em auto;
        border-width: .25em;
      }
    }
  }

  .other-products{
    .sales-slider2{
      margin-top: 1.25em;
    }
  }

  .product-card3{
    &__img{
      height: 5.3125em;
      margin-bottom: 1em;
    }
    &__title{
      font-size: .75em;
    }
  }
}
