.history{
  &__items{
    margin-top: 1.25em;
    @include grid100gap(1.25em);
  }
}

.history-item{
  border: 1px solid #E7E7E7;
  border-radius: 1.25em;
  overflow: hidden;
  &._open{
    .history-item__toggle{
      color: #9A9B9D;
      ._view1{
        display: none;
      }
      ._view2{
        display: block;
      }
    }
  }
  &__top{
    padding: .75em .875em;
    background-color: #FEFAA3;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__title{
    font-size: .875em;
    font-weight: 700;
  }
  &__status{
    margin-left: .25em;
    flex-shrink: 0;
    display: flex;
    align-items: center;
    font-weight: 700;
    color: $primary;
    span{
      font-size: .8125em;
    }
    img{
      width: 1.25em;
      margin-right: .25em;
    }
  }
  &__list{
    padding: .625em .375em;
    @include grid100gap(.625em);
  }
  &__bottom{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 .3125em .875em;
  }
  &__status2{
    height: 1.875em;
    display: flex;
    align-items: center;
    padding: 0 .625em;
    background-color: #E5E5E3;
    border-radius: 1.25em;
    font-weight: 700;
    color: #64482A;
    text-transform: uppercase;
    span{
      font-size: .6875em;
    }
  }
  &__toggle{
    font-size: .75em;
    line-height: 110%;
    color: $tango;
    border-bottom: 1px dashed $tango;
    ._view2{
      display: none;
    }
  }
  &__content{
    padding: 0 .75em 2em;
    display: none;
  }
  &__badge{
    padding: 1em;
    background-color: #F5F1E9;
    border-radius: .625em;
    font-weight: 700;
    .text14{
      line-height: 100%;
    }
  }
  &__props{
    padding: 0 .75em;
    margin-top: 1.625em;
    @include grid100gap(1.5em)
  }
  &__prop{
    @include grid100gap(.625em);
    &-title,
    &-desc{
      font-size: .875em;
    }
    &-title{
      font-weight: 700;
    }
  }
  @media screen and (min-width: 577px){
    &__top,
    &__content,
    &__bottom{
      font-size: 1.33em;
    }
  }
}

.history-product{
  padding: .5em .5em .75em;
  border-bottom: 1px solid #E7E7E7;
  &:last-child{
    border-bottom: none;
  }
  .order-item__title{
    font-family: $font2;
  }
}

.news-subscribe{
  margin-top: 1.25em;
  padding: 1em .75em;
  border: 1px dashed #F27623;
  border-radius: .625em;
  &__title{
    font-size: 1.25em;
    line-height: 130%;
    text-transform: uppercase;
    font-family: $font2;
  }
  &__fields{
    margin-top: 1.125em;
    @include grid100gap(.625em);
  }
  &__btn{
    margin-top: 1.125em;
    width: 100%;
    text-transform: uppercase;
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .news-subscribe{
    &__title{
      font-size: .875em;
    }
  }
}
