.fg {
  position: relative;
  margin: 0;
  label{
    display: block;
    font-size: 1.125em;
    line-height: 140%;
    margin-bottom: .375em;
    span{
      color: $coral-red;
    }
  }
  .nice-select,
  input,
  textarea{
    border-radius: 0;
    border: 1px solid #ddc097;
    padding: 0 1em;
    height: 2.44em;
    width: 100%;
    font-size: 1.125em;
    background-color: #fdf8f0;
    color: #000;
    &::placeholder {
      color: #a1a1a1;
    }
  }
  textarea{
    padding-top: 1em;
    padding-bottom: 1em;
    height: 4.28em;
    resize: none;
    border-color: #D6D6D6;
    background-color: transparent;
  }
  &__field{
    position: relative;
  }
  .nice-select{
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 0.1875em;
    white-space: nowrap;
    height: 3.6875em;
    padding: 0 0 0 1.25em;
    &.disabled{
      pointer-events: none;
      background: #F6F6F6 !important;
      .current{
        opacity: 0.4;
      }
    }
    &.open{
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
    &:focus{
      border: 1px solid $black;
    }
    &:before{
      content: '';
      order: 3;
      border-left: $black;
      flex-shrink: 0;
      width: 3.6875em;
      height: 3.6875em;
      background-image: url(../img/select-arrow.svg);
      @include bgImgSize(.5em);
      margin-left: 1em;
    }
    .current{
      font-size: 1em;
      line-height: 157%;
    }
    .list{
      margin-top: -1px;
      border-radius: 0 0 .1875em .1875em;
      width: calc(100% + 2px);
      left: -1px;
      border: 1px solid $black;
      background: #FFF;
      box-shadow: none;
      max-height: 14.875em;
      overflow-y: auto;
      z-index: 5;
      @include scroll;
    }
    .option{
      height: 3.6875em;
      font-size: 1em;
      line-height: 157%;
      padding: .5em 1.25em;
      display: flex;
      align-items: center;
      color: #2B2B2B;
      border: none;
      border-bottom: 1px solid $black;
      &:last-child{
        border-bottom: none;
      }
      &.disabled{
        display: none;
      }
      &:hover,
      &.focus,
      &.selected.focus{
        background-color: $primary;
        border-color: $primary;
        font-weight: 400;
        color: #fff;
      }
    }
  }
  &._error{
    input,
    textarea{
      border-color: $primary;
      color: $primary;
      &::placeholder{
        color: $primary;
      }
    }
    .fg__error-text{
      display: block;
    }
  }
  &__error-text{
    margin-top: .375em;
    color: $primary;
    display: none;
  }
}

.first-pay__item,
.radio-btn,
.color-item,
.m-radio,
.m-check{
  position: relative;
  input{
    position: absolute;
    opacity: 0;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    z-index: 2;
    cursor: pointer;
  }
}

.m-check,
.m-radio{
  color: #575652;
  label{
    position: relative;
    cursor: pointer;
    display: flex;
    align-items: center;
    padding-left: 2.125em;
    min-height: 1.25em;
    span{
      font-size: 1.125em;
      line-height: 130%;
    }
    &:before,
    &:after{
      pointer-events: none;
    }
    &:before{
      content: '';
      position: absolute;
      z-index: 1;
      left: 0;
      top: 50%;
      transform: translateY(-50%);
      border: 1px solid #767676;
      width: 1.25em;
      height: 1.25em;
      @include bgImgSize(0);
      transition: all .0s;
    }
    a{
      text-decoration: underline;
      position: relative;
      z-index: 2;
    }
  }
}

.m-check {
  input {
    &:checked+label {
      &::before {
        background-color: $primary;
        border-color: $primary;
        background-size: .75em auto;
      }
    }
  }
  label {
    &:before {
      border-radius: .1875em;
      background-image: url(../img/check2.svg);
    }
  }
  &._gray{
    label{
      color: #787878;
    }
  }
  .fg{
    position: relative;
    z-index: 2;
    margin: 0 .75em;
    input{
      position: static;
      opacity: 1;
      height: 2.44em;
      width: 3em;
      padding: 0 .25em;
      text-align: center;
    }
  }
}

.m-radio{
  input:checked~label{
    &:before{
      border: none;
      background-size: 100% auto;
    }
  }
  label{
    &:before{
      border-radius: 50%;
      background-image: url(../img/radio.svg);
    }
    span{
      display: block;
      line-height: 157%;
      color: #5B626C;
      margin-top: .125em;
    }
  }
}

.cartcalc{
  display: inline-flex;
  align-items: center;
  gap: 0.3125em;
  border-top: 0.0625em solid #E7E7E7;
  padding-top: 0.125em;
  .ccalc-minus,
  .ccalc-plus {
    width: 1.3125em;
    height: 1.3125em;
    display: flex;
    justify-content: center;
    align-items: center;
    background: linear-gradient(180.00deg, rgb(237, 237, 237),rgb(235, 235, 235) 54.13%,rgb(243, 243, 243) 55.74%,rgb(255, 255, 255) 100%);
    border: 0.09375em solid rgba(79, 79, 79, 0.5);
    box-shadow: inset 0 0 0 0.1875em rgba(255, 255, 255, 0.11);
    border-radius: 0.28125em;
    padding: 0.1875em;
    img {
      max-width: 100%;
      width: 0.8125em;
    }
  }
  .ccalc-input {
    width: 3.375em;
    height: 1.5em;
    input {
      width: 100%;
      height: 100%;
      text-align: center;
      background: #fff;
      border-color: transparent;
      font-size: 1.3125em;
      line-height: 120%;
      padding: 0;
      margin: 0;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .fg{
    label{
      font-size: .75em;
    }
    input,
    textarea{
      font-size: .75em;
      height: 2.5em;
    }
    textarea{
      height: 4.5em;
    }
  }

  .m-check{
    label{
      padding-left: 1.5em;
      &:before{
        width: .875em;
        height: .875em;
        border-radius: .125em;
      }
      span{
        font-size: .75em;
      }
    }
    .fg{
      input{
        height: 3em;
        width: 4em;
      }
    }
  }

  .cartcalc{
    gap: 0.1875em;
    .ccalc-minus,
    .ccalc-plus {
      width: 0.875em;
      height: 0.875em;
      img {
        width: 0.5em;
      }
    }
    .ccalc-input {
      width: 2.25em;
      height: 1em;
    }
  }
}
