.manufactory {
  &__title {
    text-align: center;
  }
  .search {
    position: relative;
    height: 2em;
    margin-top: 1.25em;
    input {
      width: 100%;
      height: 100%;
      border: 0.125em solid #F27623;
      border-radius: 1em;
      padding: 0 2em;
      font-size: 0.75em;
      line-height: 120%;
      &::placeholder {
        color: #A1A1A1;
      }
    }
    input[type="search"]::-webkit-search-decoration,
    input[type="search"]::-webkit-search-cancel-button,
    input[type="search"]::-webkit-search-results-button,
    input[type="search"]::-webkit-search-results-decoration {
      display: none;
    }
    &-btn {
      position: absolute;
      z-index: 1;
      top: 50%;
      right: 0.625em;
      transform: translate(0, -50%);
      width: 0.875em;
      background: transparent;
      padding: 0;
      img {
        width: 100%;
      }
    }
    &-clear {
      position: absolute;
      top: 50%;
      left: 0.40625em;
      transform: translate(0, -50%);
      width: 0.875em;
      height: 0.875em;
      display: flex;
      justify-content: center;
      align-items: center;
      background: #dcdcda;
      border-radius: 50%;
      padding: 0.125em;
      cursor: pointer;
      img {
        max-width: 100%;
      }
    }
  }
  &__links {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    gap: 0.5em;
    margin-top: 0.625em;
  }
  &__links-eng,
  &__links-ru {
    display: flex;
    gap: 0.25em;
  }
  &__link {
    font-size: 0.75em;
    line-height: 120%;
    font-weight: 700;
    color: #5C584B;
  }
  &__items {
    display: grid;
    grid-template-columns: 100%;
    gap: 1.25em;
    margin-top: 1.25em;
  }
  &__item {
    &-title {
      border-bottom: 0.0625em solid #E7E7E7;
      padding-bottom: 0.3125em;
      span {
        font-family: 'PF Agora Sans Pro';
        font-size: 1.25em;
        line-height: 120%;
        color: #F27623;
      }
    }
    &-subtitle {
      display: inline-block;
      text-decoration: underline;
      text-underline-offset: 0.0625em;
      padding-top: 0.625em;
      span {
        font-family: 'PF Agora Sans Pro';
        font-size: 1.25em;
        line-height: 120%;
      }
    }
    &-list {
      display: grid;
      grid-template-columns: 100%;
      gap: 0.625em;
      margin-top: 0.625em;
      li a {
        font-size: 0.75em;
        line-height: 120%;
        color: #5C584B;
        text-decoration: underline;
        text-underline-offset: 0.0625em;
      }
      li a:hover {
        color: #F27623;
        text-decoration: unset;
      }
    }
  }
  &__links,
  &__items,
  .search{
    @media screen and (min-width: 577px){
      font-size: 1.33em;
    }
  }
}
