.footer{
  margin-top: 2.5em;
  &__logo{
    display: block;
    width: 11.25em;
    flex-shrink: 0;
    img{
      width: 100%;
      display: block;
    }
  }
  &__pay{
    width: 17.5em;
    margin: 2.5em auto 0;
    max-width: 100%;
    img{
      width: 100%;
      display: block;
    }
  }
}

.footer-top{
  padding: 1.25em 0;
  box-shadow: inset 0 1px 12px 0 rgba(61, 57, 44, 0.11);
  background-color: $spring-wood;
  &__inner{
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__links{
    display: flex;
    flex-direction: column;
    align-items: flex-end;
  }
  &__link{
    color: $tango;
    margin-bottom: .25em;
    display: flex;
    align-items: center;
    &:last-child{
      margin: 0;
      &:before{
        content: '/';
        font-size: 1em;
        margin-right: .75em;
        color: $ash;
      }
    }
    span{
      text-decoration: underline;
      font-size: .9375em;
      line-height: 112%;
    }
    img{
      width: .75em;
      margin-right: .5em;
      flex-shrink: 0;
    }
  }
}

.footer-contacts{
  text-align: center;
  font-family: $font2;
  &__title{
    font-size: .75em;
    line-height: 140%;
    font-weight: 700;
    color: $bison-hide;
  }
  &__phone{
    font-size: 1.375em;
    line-height: 140%;
    font-weight: 700;
  }
  &__bottom{
    font-weight: 500;
    font-size: .875em;
    line-height: 140%;
  }
}

.footer-bottom{
  padding: 1.25em 0 1.5em;
  box-shadow: inset 0 1px 3px 0 rgba(61, 57, 44, 0.2);
  background-color: $aths-special;
}

.footer-menu{
  display: grid;
  grid-template-columns: repeat(2,calc(50% - 1.25em));
  gap: 2.5em;
}

.footer-nav{
  @media screen and (min-width: 577px){
    &._type2{
      .footer-nav__links{
        grid-template-columns: 1fr 1.4fr;
      }
    }
    &._type3{
      grid-column: 1/-1;
      .footer-nav__links{
        grid-template-columns: 1fr 1.7fr;
      }
    }
  }
  &__title{
    font-size: 1.5em;
    text-transform: uppercase;
    font-family: $font2;
    font-weight: 700;
  }
  &__links{
    margin-top: 1em;
    display: grid;
    grid-template-columns: repeat(2,calc(50% - .25em));
    gap: .5em;
    &-group{
      display: flex;
      flex-direction: column;
      align-items: flex-start;
    }
  }
  &__link{
    font-size: 1.125em;
    line-height: 133%;
    text-decoration: underline;
    color: $fuscous-gray;
    margin-bottom: .5em;
    &:last-child{
      margin: 0;
    }
  }
}

.footer-info{
  margin-top: 2.5em;
  text-align: center;
  @include grid100gap(.5em);
  &__item{
    font-size: 1em;
    line-height: 105%;
    a{
      color: $tango;
      text-decoration: underline;
    }
  }
}


@media screen and (max-width: 992px){

}


@media screen and (max-width: 576px){
  .footer{
    margin-top: 1.25em;
    &__logo{
      margin: 0 auto;
    }
  }

  .footer-top{
    padding-bottom: 1.875em;
    &__inner{
      @include grid100gap(1.25em);
    }
    &__links{
      flex-direction: row;
      align-items: center;
      justify-content: center;
    }
    &__link{
      margin: 0 .625em 0 0;
      &:last-child{
        &:before{
          font-size: .75em;
        }
      }
      span{
        font-size: .75em;
      }
    }
  }

  .footer-menu{
    grid-template-columns: 100%;
  }

  .footer-nav{
    &__title{
      font-size: .875em;
    }
    &__links{
      grid-template-columns: 5.5em calc(100% - 7em);
      gap: 1.5em;
      &-group{

      }
    }
    &__link{
      font-size: .75em;
    }
    &._type3{
      .footer-nav__links{
        width: calc(100% + 1em);
      }
    }
  }

  .footer-info{
    text-align: left;
    gap: 0;
    &__item{
      font-size: .75em;
      line-height: 140%;
    }
  }
}
