.filter-slider{
  &__item{
    margin-top: .75em;
    min-height: 1em;
    padding: 0 .125em;
    display: flex;
    align-items: center;
  }
  &__output{
    font-size: .75em;
    line-height: 140%;
    color: $fuscous-gray;
  }
  .range-slider{
    width: 100%;
    background: rgb(248, 248, 248);
    border: 1px solid rgb(222, 222, 222);
    box-shadow: none;
    height: .3125em;
    border-radius: .125em;
    .noUi-connects{
      height: calc(100% + 2px);
      margin-top: -1px;
    }
    .noUi-connect{
      background: linear-gradient(180.00deg, rgba(255, 255, 255, 0),rgba(255, 255, 255, 0.35) 100%),rgb(241, 109, 33);
    }
    .noUi-handle{
      box-shadow: inset 0px 2px 1px 1px rgba(9, 9, 10, 0.03),0px 1px 3px 0px rgba(9, 9, 10, 0.23);
      border: none;
      background-color: transparent;
      background-image: url(../img/slider-btn.svg);
      @include bgImgSize(100%);
      width: .4375em;
      height: 1.0625em;
      top: -.375em !important;
      right: -.1875em !important;
      border-radius: 0;
      cursor: pointer;
      border-radius: .125em;
      &:before{
        opacity: 0;
        width: 300%;
        height: 100%;
        @include centerAbsolute;
      }
      &:after{
        display: none;
      }
    }
  }
}
